  
  import {apiLink}  from "../api/apiConstant";
  
  const About = ()=>{

    const postHandler = ()=>{
      const url=`${apiLink}addUser`
     
      fetch(url, {
  method: 'POST',
  body: JSON.stringify({
    username:"Ram shiva",
    email: "all is well",
    phone:"9988667755",

  }),
  headers: {
    'Content-type': 'application/json; charset=UTF-8',
  }
  })
  .then(function(response){ 
  return response.json()})
  .then(function(data)
  {console.log(data)
    
}).catch(error => console.error('Error:', error)); 

}
    return (
      <>
        <h1>About PAGE FOUND</h1>
        <button type="button" onClick={postHandler} >post </button>
        </>
    )
}

export default About